$link-color: #61dafb;

.App {
  text-align: center;

  a {
    color: $link-color;
    margin: 10px 0;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-bottom: 2em;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 10px;
  box-sizing: border-box;
}

.App-link {
  color: $link-color;
}
